@import url(https://fonts.googleapis.com/css2?family=Oxanium:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oxanium:wght@800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
  font-family: 'Oxanium', cursive;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.content-wrapper {
  overflow: auto;
  -ms-scroll-snap-type: y mandatory;
      scroll-snap-type: y mandatory;
}

.titlebar {
    display: flex;
    background-color: #011936;
    font-family: 'Oxanium', cursive;
    justify-content: space-between;
    padding-left: 20px;
    padding-right: 75px;
    position: fixed;
    width: 100%;
    height: 120px;
}

.icons {
    justify-content: center;
    align-self: center;
}

.icons img {
    margin-right: 20px;
    height: 40px;
    width: auto;
}

.links {
    align-self: center;
    margin-right: 20px;
}

.links a {
    color: white;
    text-decoration: none;
    margin-right: 20px;
    font-size: x-large;
}

.greeting-wrapper {
    height: 100vh;
    background-image: url(/portfolio/static/media/mthoodgray.35670155.png);
    margin-top: 120px;
}

.greeting {
    width: 25%;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    background-color: #465362;
    border-radius: 5%;
    margin-left: 5%;
    border: white 2px solid;
}

.greeting img {
    width: 50%;
    height: auto;
    border-radius: 50%;
    border: white 5px solid;
}

.greeting p {
    text-align: center;
    color: white;
}
.projects-wrapper {
    background-color: white;
    scroll-snap-align: start;
    height: 100vh;
    background-color: #011936;
}

.project-list {
    display: grid;
    justify-content: center;
    grid-template-columns: 300px 300px 300px;
    grid-template-rows: 300px 300px 300px;
    grid-gap: 50px;
    margin-top: 120px;
}
.project-card {
    background-color: white;
    border-radius: 10%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.contact {
    height: 100vh;
    background-color: #465362;
    display: flex;
    justify-content: center;
}

.contact-form-wrapper {
    display: flex;
    justify-content: center;
    margin-top: 120px;
    background-color: white;
    width: 300px;
    height: 300px;
    border-radius: 5%;
}

form {
    display: flex;
    flex-direction: column;
}
